.header {
  box-sizing: border-box;
  background-color: #fff;
  width: 100%;
  padding-left: 50px;
  padding-right: 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header__img {
  width: 40px;
  height: 40px;
}

.header__nav {
  justify-content: flex-end;
  margin-left: auto;
}

.header__nav-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.header__nav-list :not(:last-child) {
  margin-right: 20px;
}

.header__nav-list-item {
  position: relative;
}

.header__nav-list-item-link {
  text-transform: uppercase;
  word-wrap: break-word;
  color: #060b35;
  justify-content: center;
  align-items: center;
  height: 60px;
  text-decoration: none;
  display: flex;
}

.header__nav-list-item-link:hover {
  color: #00acdc;
}

.is-active {
  color: #00acdc;
  flex-direction: column;
}

.is-active:after {
  content: "";
  background: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
}

.container {
  box-sizing: border-box;
  justify-content: center;
  place-items: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media only screen and (width <= 487px) {
  .container {
    grid-template-columns: repeat(1, 200px);
  }
}

@media only screen and (width >= 488px) and (width <= 767px) {
  .container {
    grid-template-columns: repeat(2, 200px);
  }
}

@media only screen and (width >= 768px) and (width <= 1023px) {
  .container {
    grid-template-columns: repeat(3, 200px);
  }
}

@media only screen and (width >= 1024px) {
  .container {
    grid-template-columns: repeat(4, 200px);
  }
}

.card {
  color: #060b35;
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  padding: 32px 16px 16px;
  line-height: 18px;
  transition: all .3s;
  display: flex;
}

.card__image {
  align-self: center;
  width: 160px;
  height: 134px;
  margin-bottom: 40px;
}

.card__title {
  box-sizing: border-box;
  flex-direction: column;
  align-items: center;
  margin: 0;
  font-size: 12px;
  display: flex;
}

.card .card__title:hover {
  color: #34568b;
}

.card__code {
  color: #616070;
  margin-top: 4px;
  margin-bottom: 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__review {
  justify-content: space-between;
  line-height: 14px;
  display: flex;
}

.card__review-reviews {
  margin-left: auto;
  font-size: 10px;
  font-weight: 400;
}

.card__value {
  justify-content: space-between;
  height: 18px;
  margin-top: 24px;
  margin-bottom: 16px;
  font-size: 12px;
  font-weight: 400;
  display: flex;
}

.card__value-cost {
  font-size: 16px;
  font-weight: 700;
  display: block;
}

.card__buy {
  box-sizing: border-box;
  text-transform: uppercase;
  color: #fff;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  text-decoration: none;
  transition: all .3s;
  display: flex;
}

.card__buy:hover {
  color: #00acdc;
  background-color: #fff;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star-active.edda4c43.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.stars__star:not(:last-child) {
  margin-right: 4px;
}

.stars--1 .stars__star:nth-child(n+2), .stars--2 .stars__star:nth-child(n+3), .stars--3 .stars__star:nth-child(n+4), .stars--4 .stars__star:nth-child(n+5) {
  background-image: url("star.822d561e.svg");
}

body {
  color: #060b35;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
}

